<template>
    <v-sheet>
        <plain-data
                title="급여 지급내역"
                data-id="pay"
                :items-per-page="10"
                :headers="headers"
                :selectItems="selectItems"
                :sortBy="['강사명']"
                class="user-table"

        >
            <template v-slot:item.강의료="{ item }">
                <div class="text-right">{{ item.강의료 | comma }}</div>
            </template>
            <template v-slot:item.본인부담합계="{ item }">
                <div class="text-right">{{ item.본인부담합계 | comma }}</div>
            </template>
            <template v-slot:item.실수령액="{ item }">
                <div class="text-right">{{ item.실수령액 | comma }}</div>
            </template>
        </plain-data>
    </v-sheet>
</template>
<script>
    import PlainData from "@/components/PlainData.vue"
    // import UserView from "@/components/UserView.vue"
    // import UserCreate from "@/components/UserCreate.vue"
    export default {
        components: {
            PlainData,
            // UserView,
            // UserCreate
        },
        data: function () {
            return {
                headers: [
                    {text: "ID", align: "center", value: "id", sortable: true},
                    {text: "지급구분", align: "center", value: "지급구분", sortable: true},
                    {text: "지급일", align: "center", value: "지급일", sortable: true},
                    {text: "No", align: "center", value: "No", sortable: true},
                    {text: "강사명", align: "center", value: "강사명", sortable: true},
                    {text: "학교명", align: "center", value: "학교명", sortable: true},
                    {text: "과목명", align: "center", value: "과목명", sortable: true},
                    {text: "강의료", align: "center", value: "강의료", sortable: true},
                    {text: "본인부담", align: "center", value: "본인부담합계", sortable: true},
                    {text: "실수령액", align: "center", value: "실수령액", sortable: true},
                    {text: "메일주소", align: "center", value: "메일주소", sortable: true},
                    {text: "생성일시", align: "center", value: "created", sortable: true},
                    {text: "발송결과", align: "center", value: "발송결과", sortable: true}
                ],
                selectItems: [
                    { text: "지급구분", value: "지급구분", default: true },
                    { text: "강사명", value: "강사명", default: true },
                    { text: "학교명", value: "학교명", default: true },
                    { text: "과목명", value: "과목명", default: true }
                ],
                selected: []
            }
        },
        methods: {

        },
        filters:{
            comma(val){
                return String(val).replace(/\B(?=(\d{3})+(?!\d))/g, ",");
            }
        }
    }
</script>
