<template>
    <v-sheet class="pa-5">
        <v-card flat>
            <v-card-title>
                메일 보내기
            </v-card-title>
            <v-card-text>

                <v-tabs v-model="tab">
                    <v-tab key="test">테스트</v-tab>
                    <v-tab key="send">발송</v-tab>
                </v-tabs>
                <v-tabs-items v-model="tab">
                    <v-tab-item key="test">
                        <v-sheet max-width="400" class="mt-10 pa-3">

                            <validation-observer
                                    ref="observer"
                                    v-slot="{ invalid, validated, handleSubmit }"
                            >
                                <div class="mb-10">테스트용 메일 주소로 급여 명세서를 발송합니다.</div>
                                <v-row>
                                    <v-col>
                                        <validation-provider name="대상 강사 인원" :rules="{ required: true, numeric: true }" v-slot="{ errors, valid }">
                                            <v-text-field
                                                    label="대상 강사 인원(명)"
                                                    v-model="limit"
                                                    outlined
                                                    append-icon="mdi-human-male-female"
                                                    :error-messages="errors" :success="valid"
                                            >
                                            </v-text-field>
                                        </validation-provider>
                                    </v-col>
                                    <v-col>
                                        <validation-provider name="옵셋" :rules="{ required: true, numeric: true }" v-slot="{ errors, valid }">
                                            <v-text-field
                                                    label="옵셋 인원"
                                                    v-model="offset"
                                                    outlined
                                                    append-icon="mdi-keyboard-tab"
                                                    :error-messages="errors" :success="valid"
                                            >
                                            </v-text-field>
                                        </validation-provider>
                                    </v-col>
                                </v-row>
                                <validation-provider name="테스트 메일 주소" :rules="{ required: true, email: true }" v-slot="{ errors, valid }">
                                    <v-text-field
                                            label="테스트 메일 주소"
                                            v-model="to"
                                            outlined
                                            append-icon="mdi-email-outline"
                                            :error-messages="errors" :success="valid"
                                    >
                                    </v-text-field>
                                </validation-provider>
                                <validation-provider name="메일 제목" :rules="{ required: true }" v-slot="{ errors, valid }">
                                    <v-text-field
                                            label="메일 제목"
                                            v-model="subject"
                                            outlined
                                            append-icon="mdi-dock-top"
                                            :error-messages="errors" :success="valid"
                                    >
                                    </v-text-field>
                                </validation-provider>
                                <v-btn color="primary" elevation="0" @click="handleSubmit(testSubmit)">테스트 보내기</v-btn>
                                <v-dialog
                                        v-model="dialog"
                                        width="500"
                                >
                                    <v-card>
                                        <v-card-title class="grey lighten-2">
                                            발송 결과
                                        </v-card-title>

                                        <v-card-text class="pa-10 text-center">
                                            <div :class="messageClass">{{ message }}</div>

                                            <v-progress-linear
                                                    v-model="percent"
                                                    color="lime"
                                                    height="20"
                                                    style="width: 250px; margin: auto;"
                                                    class="mx-auto my-2"
                                            >
                                                <template v-slot:default="{ value }">
                                                    <strong>{{ Math.ceil(value) }}%</strong>
                                                </template>
                                            </v-progress-linear>
                                            <div class="text-overline pink--text">{{ report }}</div>
                                        </v-card-text>

                                        <v-divider></v-divider>

                                        <v-card-actions>
                                            <v-spacer></v-spacer>
                                            <v-btn
                                                    color="primary"
                                                    elevation="0"
                                                    @click="dialog = false"
                                            >
                                                확인
                                            </v-btn>
                                        </v-card-actions>
                                    </v-card>
                                </v-dialog>

                            </validation-observer>
                        </v-sheet>
                    </v-tab-item>
                    <v-tab-item key="send">
                        <v-sheet max-width="400" class="mt-10 pa-3">

                            <validation-observer
                                    ref="observer2"
                                    v-slot="{ invalid, validated, handleSubmit }"
                            >
                                <div class="mb-10">실제 대상자 메일 주소로 급여 명세서를 발송합니다.</div>
                                <v-row>
                                    <v-col>
                                        <validation-provider name="대상 강사 인원" :rules="{ required: true, numeric: true }" v-slot="{ errors, valid }">
                                            <v-text-field
                                                    label="대상 강사 인원(명)"
                                                    v-model="limit2"
                                                    outlined
                                                    append-icon="mdi-human-male-female"
                                                    :error-messages="errors" :success="valid"
                                            >
                                            </v-text-field>
                                        </validation-provider>
                                    </v-col>
                                    <v-col>
                                        <validation-provider name="옵셋" :rules="{ required: true, numeric: true }" v-slot="{ errors, valid }">
                                            <v-text-field
                                                    label="옵셋 인원"
                                                    v-model="offset2"
                                                    outlined
                                                    append-icon="mdi-keyboard-tab"
                                                    :error-messages="errors" :success="valid"
                                            >
                                            </v-text-field>
                                        </validation-provider>
                                    </v-col>
                                </v-row>
                                <validation-provider name="메일 제목" :rules="{ required: true }" v-slot="{ errors, valid }">
                                    <v-text-field
                                            label="메일 제목"
                                            v-model="subject2"
                                            outlined
                                            append-icon="mdi-dock-top"
                                            :error-messages="errors" :success="valid"
                                    >
                                    </v-text-field>
                                </validation-provider>
                                <v-btn color="primary" elevation="0" @click="handleSubmit(submit)">메일 보내기</v-btn>
                                <v-dialog
                                        v-model="dialog"
                                        width="500"
                                >
                                    <v-card>
                                        <v-card-title class="grey lighten-2">
                                            발송 결과
                                        </v-card-title>

                                        <v-card-text class="pa-10 text-center">
                                            <div :class="messageClass">{{ message }}</div>

                                            <v-progress-linear
                                                    v-model="percent"
                                                    color="yellow"
                                                    height="20"
                                                    style="width: 250px; margin: auto;"
                                                    class="mx-auto my-2"
                                            >
                                                <template v-slot:default="{ value }">
                                                    <div class="text-overline">{{ Math.ceil(value) }}%</div>
                                                </template>
                                            </v-progress-linear>
                                            <div class="text-overline pink--text">{{ report }}</div>
                                        </v-card-text>

                                        <v-divider></v-divider>

                                        <v-card-actions>
                                            <v-spacer></v-spacer>
                                            <v-btn
                                                    color="primary"
                                                    elevation="0"
                                                    @click="dialog = false"
                                            >
                                                확인
                                            </v-btn>
                                        </v-card-actions>
                                    </v-card>
                                </v-dialog>

                            </validation-observer>
                        </v-sheet>
                    </v-tab-item>
                </v-tabs-items>
            </v-card-text>
        </v-card>
    </v-sheet>
</template>
<script>
    import io from 'socket.io-client'

    export default {
        data() {
            return {
                dialog: false,
                tab: null,
                limit: 3,
                limit2: 3,
                offset: 0,
                offset2: 0,
                to: "jhoon2002@naver.com",
                subject: "[한예종 산단]2021년 10월 급여 명세서",
                subject2: "[한예종 산단]2021년 10월 급여 명세서",
                message: "",
                messageClass: {
                    'pink--text': false,
                    'blue--text': false
                },
                totalCount: 0,
                resultCount: 0,
                socket : io(process.env.VUE_APP_SOCKET_URL, { transports : ['websocket'] })
            }
        },
        mounted() {
            //console.log("process.env.VUE_APP_SOCKET_URL: ", process.env.VUE_APP_SOCKET_URL)
            // 서버의 변경사항을 수신
            this.socket.on('MESSAGE', (data) => {
                console.log(data)
                this.totalCount = data.totalCount
                this.resultCount = data.resultCount
            })
        },
        computed: {
            report() {
                return this.resultCount + '/' + this.totalCount
            },
            percent() {
                const ratio = Math.floor( (this.resultCount / this.totalCount) * 100 )
                return ratio ? ratio : 0
            }
        },
        methods: {
            async testSubmit() {
                await this.sendMail(true)
                return
            },
            async submit() {
                await this.sendMail(false)
                return
            },
            async sendMail(isTest) {

                this.resultCount = 0
                this.totalCount = 0

                const interval = setInterval( () => {
                    this.socket.emit('SEND_MESSAGE')
                }, 1000)

                this.dialog = true
                this.message = '메일을 발송 중입니다.'
                this.messageClass["blue--text"] = false
                this.messageClass["pink--text"] = false
                try {
                    await this.$http.post(isTest ? "/api/pays/test-send-mail" : "/api/pays/send-mail", {
                        limit: isTest ? this.limit : this.limit2,
                        offset: isTest ? this.offset : this.offset2,
                        to: this.to,
                        subject: isTest ? this.subject : this.subject2
                    }, { timeout: 3600000}) //1시간 timeout 적용
                    this.message = '메일 발송을 완료하였습니다.'
                    this.messageClass["blue--text"] = true
                    this.messageClass["pink--text"] = false
                } catch(e) {
                    this.message = "서버 문제로 메일을 발송하지 못했습니다."
                    this.messageClass["blue--text"] = false
                    this.messageClass["pink--text"] = true
                    console.log(e)
                } finally {
                    this.socket.emit('SEND_MESSAGE')
                    clearInterval(interval)
                }
                return
            },
        }
    }
</script>