<template>
    <v-sheet width="500" class="pa-5">
        <v-card flat>
            <v-card-title>
                <span class="text-h5">급여 업로드</span>
            </v-card-title>

            <v-card-text>
                <validation-observer
                        ref="observer"
                        v-slot="{ invalid, validated, handleSubmit }"
                >
                    <validation-provider name="입력 방법" :rules="{ required: true }" v-slot="{ errors, valid }">
                        <v-radio-group v-model="by">
                            <v-radio
                                    label="기존 데이터: 삭제"
                                    value="delete"
                                    :error-messages="errors" :success="valid"
                            ></v-radio>
                        </v-radio-group>
                    </validation-provider>
                    <validation-provider name="파일" :rules="{ required: true }" v-slot="{ errors, valid }">
                        <v-file-input
                                v-model="file"
                                show-size
                                small-chips
                                truncate-length="30"
                                ref="file"
                                accept="application/vnd.ms-excel, application/vnd.openxmlformats-officedocument.spreadsheetml.sheet"
                                :error-messages="errors" :success="valid"
                        ></v-file-input>
                    </validation-provider>
                    <v-btn
                            color="primary"
                            elevation="0"
                            class="mt-8"
                            @click="handleSubmit(submit)"
                    >
                        확인
                    </v-btn>
                    <v-dialog
                            v-model="dialog"
                            width="500"
                    >
                        <v-card>
                            <v-card-title class="grey lighten-2">
                                입력 결과
                            </v-card-title>

                            <v-card-text class="pa-10">
                                {{message}}
                            </v-card-text>

                            <v-divider></v-divider>

                            <v-card-actions>
                                <v-spacer></v-spacer>
                                <v-btn
                                        color="primary"
                                        elevation="0"
                                        @click="dialog = false"
                                >
                                    확인
                                </v-btn>
                            </v-card-actions>
                        </v-card>
                    </v-dialog>
                </validation-observer>
            </v-card-text>
        </v-card>
    </v-sheet>
</template>
<script>
    export default {
        data () {
            return {
                by: "delete",
                file: null,
                dialog: false,
                loading: false,
                message: ""
            }
        },
        methods: {
            async submit() {
                 //this.$refs.observer.validate()

                /*
                    [file: OBJ]
                    lastModified: 1633339516488
                    lastModifiedDate: Mon Oct 04 2021 18:25:16 GMT+0900 (한국 표준시) {}
                    name: "radeon-software-adrenalin-2020-21.9.2-minimalsetup-210920_web.exe"
                    size: 39090456
                    type: "application/x-msdownload"
                    webkitRelativePath: ""
                 */
                //console.log(this.file)

                this.loading = true

                const form = new FormData();
                form.append('file', this.file)
                try {
                    await this.$http.delete("/api/pays")
                    const ret = await this.$http.post("/api/file/upload", form, {
                        headers: {
                            'Content-Type': 'multipart/form-data'
                        }
                    })
                    this.message = "종전 자료를 삭제한 후, 총" + ret.data.count + "건을 새로 저장하였습니다."
                    this.dialog = true
                } catch(e) {
                    this.message = "적합하지 않은 파일입니다.(종전 자료는 삭제)"
                    this.dialog = true
                    console.log(e)
                } finally {
                    await setTimeout(() => (this.loading = false), 500)
                }

            }
        }
    }
</script>